import React, { useState, useEffect } from "react";
import { Urlloginadmin } from "../assets/servicios/Url";
import { Container, Form, Button } from "react-bootstrap";
import md5 from "md5";


const LoginAdmin = () => {

    const md5 = require("md5");

    const [usuario, setUsuario] = useState("")
    const [pass, setPass] = useState("")


    const [existe, setExiste] = useState(true);


    const loginUsuario = (e) => {
        fetch(Urlloginadmin + "?usuario=" + usuario + "&password=" + md5(pass))
            .then((r) => r.json())
            .then(r => {
                if (r.success === 0) {
                    console.log(r)
                    setExiste(true)
                    setUsuario(r)
                }
                else {
                    alert("Usuario o password incorrectos.");
                    setExiste(false)
                }
            })

            .catch(err => {
                console.log(err)
            })
    }

    return (
        <>
            <Container>
                <Form>
                    <Form.Group className="mb-3" controlId="usuario">
                        <Form.Label>Usuario:</Form.Label>
                        <Form.Control onChange={(e) => { setUsuario(e.target.value) }} type="text" placeholder="usuario" />
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="pass">
                        <Form.Label>Contrasea:</Form.Label>
                        <Form.Control onChange={(e) => { setPass(e.target.value) }} type="password" placeholder="*****" />
                    </Form.Group>
                    <Button variant="primary" onClick={loginUsuario}>
                        Iniciar sesin
                    </Button>
                    {
                        !existe ?
                            (
                                <div className="alert-danger text-center">{"Email o password incorrectos."}</div>
                            ) :
                            (
                                <span />
                            )
                    }
                </Form>
            </Container>
        </>
    )
}

export default LoginAdmin;