import { Container } from 'bootstrap-4-react/lib/components/layout';
import { Carousel, Button, ProgressBar, ListGroup, Modal } from 'react-bootstrap';
import React, { Component } from 'react';
import primeraCarrusel from '../assets/images/Detalles/primeraCarrusel.jpg';
import segundaCarrusel from '../assets/images/Detalles/segundaCarrusel.jpg';
import terceraCarrusel from '../assets/images/Detalles/terceraCarrusel.jpg';
import ModalReact from './ModalReact';
import { Link } from 'react-router-dom';
import Reserva from './Reserva';
import { Urlprecio } from '../assets/servicios/Url';

class Home extends Component {
    constructor(props){
        super(props);
        this.state = {precios:[]};
    }
    componentDidMount(){
        fetch(Urlprecio)
        .then(r => r.json())
        .then(data=>{
            this.setState({precios:data})
            console.log(this.state.precios);
        })
    }
    render() {
        return (
            <Container>
                <Carousel style={{ marginTop: "50px" }}>
                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src={primeraCarrusel}
                            alt="First slide"
                            style={{ width: "1290px", height: "700px" }}
                        />
                        <Carousel.Caption>
                            <h1>Bienvenidos a nuestra nueva web!</h1>
                            <h3>Estbamos ansiosos por que nos visitaras.</h3>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src={segundaCarrusel}
                            alt="Second slide"
                            style={{ width: "1290px", height: "700px" }}
                        />
 
                        <Carousel.Caption>
                            <h1>Hazte socio y paga menos</h1>
                            <ModalReact precios={this.state.precios}/>
                        </Carousel.Caption>
                    </Carousel.Item>
                    <Carousel.Item>
                        <img
                            className="d-block w-100"
                            src={terceraCarrusel}
                            alt="Third slide"
                            style={{ width: "1290px", height: "700px" }}
                        />

                        <Carousel.Caption>
                            <h1>Ests preparado para jugar en nuestro club?</h1>
                            <Link to={"/reserva"}><Button variant="success" size="lg" type="button">Reserva aqu.</Button></Link>
                        </Carousel.Caption>
                    </Carousel.Item>
                </Carousel>

                <ProgressBar animated now={100} style={{ marginTop: "50px", marginBottom: "50px" }} />

                <h1 style={{ fontWeight: "bold", textAlign: "center", color: "#1d498f" }}>Para vosotros, lo ms importante.</h1>
                <br />

                <ListGroup variant='flush'>
                    <ListGroup.Item>Nuestro objetivo es que disfrutes del pdel en estado puro, no slo por poner a tu disposicin las mejores instalaciones para su prctica, sino tambin por estar orientados a un servicio de calidad.</ListGroup.Item>
                    <ListGroup.Item>Nuestras pistas son las de ltima generacin que ofrece el mercado, siendo las mismas que las utilizadas en el circuito oficial World Padel Tour.</ListGroup.Item>
                    <ListGroup.Item>Sus instalaciones han sido concebidas desde su origen para la prctica del pdel. Ni la climatologa, ni la altura de la cubierta, ni la temperatura impedirn que saques el mximo partido a tu experiencia de juego.</ListGroup.Item>
                    <ListGroup.Item>Cinco pistas de cristal de mxima calidad con las medidas oficiales preparadas para dar respuesta a las exigencias de los jugadores ms avanzados y de los que buscan diversin.</ListGroup.Item>
                    <ListGroup.Item>Contamos con profesionales acreditados en sus respectivos mbitos de actuacin, los cuales te invitamos a conocer.</ListGroup.Item>
                </ListGroup>


            </Container>
        );
    }
}

export default Home;